@import "variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px){
  body{
    .header__fixedNav{
      ul{
        li{
          &:nth-child(4){
            width: 10%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    .header__fixedNav{
      ul{
        li{
          &:nth-child(1){
            a{
              i{
                padding: 0 5px 0 10px;
              }
            }
          }
          &:nth-child(2) {
            .desktop{
              input {
                width: 80%;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1150px), screen and (max-device-width: 1150px){
  body{
    .header__fixedNav{
      ul{
        li{
          a{
            font-size: 1.3125em;
          }
          &:nth-child(2){
            .desktop{
              input{
                font-size: 1.1875em;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .header__fixedNav{
      width: 100%;
      margin-left: -50%;
    }
  }
}

@media screen and (max-width: 900px), screen and (max-device-width: 900px){
  body{
    .header__fixedNav{
      ul{
        li{
          a{
            i{
              font-size: 2em;
            }
          }
          &:nth-child(1),
          &:nth-child(3),
          &:nth-child(5){
            .desktop{
              display: none;
            }
            .mobile{
              display: flex;
            }
          }
          &:nth-child(1){
            width: 80px;
            a{
              i{
                padding: 0;
              }
            }
          }
          &:nth-child(2){
            width: 60%;
            .desktop{
              i{
                left: -35px;
              }
            }
          }
          &:nth-child(3),
          &:nth-child(5){
            width: 10%;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 500px), screen and (max-device-width: 500px){
  body{
    .header__fixedNav{
      ul{
        li{
          a{
            font-size: 1.25em;
          }
          &:nth-child(2),
          &:nth-child(3),
          &:nth-child(4),
          &:nth-child(5){
            width: 25%;
          }
          &:nth-child(2){
            .desktop{
              display: none;
            }
            .mobile{
              display: flex;
            }
          }
        }
      }
    }
  }
}